body {
  margin: 0;
  padding: 0;
  min-width: 100%;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

a {
  text-decoration: none;
  color: #2b2a2a;
  cursor: pointer; }
  a:hover {
    color: #656565; }

h1, h2, h3 {
  padding: 0;
  margin: 0; }

.small {
  font-size: 0.8em;
  color: #2b2a2a; }

.banner {
  position: relative;
  height: 400px; }
  .banner .banner__photos {
    height: 50%; }
  .banner .banner__whitespace {
    height: 50%; }
  .banner .banner__user__details {
    position: absolute;
    top: calc(50% - 150px);
    left: calc(50% - 300px);
    width: 600px;
    height: 300px;
    display: flex;
    justify-content: center; }
    .banner .banner__user__details .banner__user__photo {
      display: flex;
      align-items: center; }
      .banner .banner__user__details .banner__user__photo img {
        border-radius: 50%;
        border: 5px solid #ffffff; }
    .banner .banner__user__details .banner__user__info {
      display: flex;
      flex-flow: column;
      justify-content: space-evenly; }
      .banner .banner__user__details .banner__user__info__top {
        height: 50%;
        display: flex;
        align-items: flex-end;
        justify-content: space-around;
        color: #ffffff; }
      .banner .banner__user__details .banner__user__info__bottom {
        height: 50%;
        display: flex;
        flex-flow: column;
        justify-content: space-evenly; }
        .banner .banner__user__details .banner__user__info__bottom div {
          padding: 3px; }
        .banner .banner__user__details .banner__user__info__bottom .name {
          font-size: 1.2em;
          font-weight: bold; }
        .banner .banner__user__details .banner__user__info__bottom .location, .banner .banner__user__details .banner__user__info__bottom .links {
          font-size: 0.9em; }
        .banner .banner__user__details .banner__user__info__bottom .tag {
          display: inline-block;
          font-size: 0.7em;
          padding: 0.5em;
          margin: 0.2rem;
          background-color: #ffffff;
          border: none;
          border-radius: 4px;
          background-color: #888888;
          cursor: pointer; }

/* 
  ##Device = Most of the Smartphones Mobiles (Portrait)
  ##Screen = B/w 320px to 479px
*/
@media (min-width: 320px) and (max-width: 480px) {
  .banner__user__details {
    width: 100% !important;
    left: 0 !important;
    flex-flow: column;
    justify-content: center; }
    .banner__user__details .banner__user__photo {
      flex-flow: row;
      justify-content: center;
      align-items: flex-end; }
    .banner__user__details .banner__user__info {
      align-items: center; }
      .banner__user__details .banner__user__info__top div {
        display: none; }
      .banner__user__details .banner__user__info__bottom div {
        text-align: center; } }
